import axios from 'axios'

const state  = {
    pagination: {
        length: 0,
        perPage: 6, // perPage 9
        page: 1, // default page is 1st
    },
    portfolios: [],
    categoryId: null,
    catePaginate: false
}

const getters = {
    portfolios: (state) => state.portfolios,
    pagination: (state) => state.pagination,
    categoryId: (state) => state.categoryId,
    catePaginate: (state) => state.catePaginate
}

const mutations = {
    SetPORTFOLIO (state, payload) {
        state.portfolios = payload
    },
    SetLENGTH (state, payload) {
        state.pagination.length = payload
    },
    SetPAGE (state, payload) {
        state.pagination.page = payload
    },
    SetCATEGORY (state, payload) {
        state.categoryId = payload
    },
    SetCATEPAGINATE (state, payload) {
        state.catePaginate = payload
    },
    PushPORTFOLIO (state, payload) {
        state.portfolios.push(payload)
    },
    RemovePORTFOLIO (state) {
        state.portfolios = []
    }
}

const actions = {
    async fetchPortfolios (context, page) { // page is pa 1,2,3
        try {
            const { perPage } = context.state.pagination
            const results = await axios.get('home',{
                params: {
                    page,
                    perPage
                }})
            const portfolios = results.data.portfolios
            const length = results.data.length
            await context.commit('RemovePORTFOLIO')
            context.commit('SetPORTFOLIO', portfolios)
            context.commit('SetLENGTH', length)
            context.commit('SetCATEPAGINATE', false)
            context.commit('SetCATEGORY', 'all')
        } catch (e) {
            throw e
        }
    },
    async filterCategory (context, page) {
        try {
            const { perPage } = context.state.pagination
            const categoryId = context.state.categoryId
            const results = await axios.get('home/filtercategory',
                {
                    params: {
                        page,
                        perPage,
                        categoryId
                    }})
            const portfolios = results.data.portfolios
            const length = results.data.length
            await context.commit('RemovePORTFOLIO')
            context.commit('SetPORTFOLIO', portfolios) // set portfolios
            context.commit('SetLENGTH', length) // set length paginate
            context.commit('SetCATEPAGINATE', true) // turn on category paginate
            context.commit('SetCATEGORY', categoryId ) // set categoryId in vuex
            context.commit('SetPAGE', page) // set page in vuex
        }catch (e) {
            throw e
        }
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}
