import Vue from 'vue'
import axios from 'axios' // import new axios
import qs from 'qs' // query JSON stringfy + security
import '../../plugins/googlesignin' // Google signIn


const state = {
   token: localStorage.getItem('access_token') || null,
    auth: localStorage.getItem('auth_item') || {},
}

const getters = {
    signInned: (state) => state.token !== null,
    auth: (state) => qs.parse(state.auth)
}

const mutations = {
    SetTOKEN (state, token) {
        state.token = token
    },
    DestroyTOKEN (state) {
        state.token = null
    },
    SetAUTH (state, auth) {
        state.auth = auth
    },
    DestroyAUTH (state) {
        state.auth = {}
    }
}

const actions = {
        async fetchAuth (context) { // Fetch data current user
            if(context.getters.signInned) { // Only Auth
                axios.defaults.headers.common['Authorization'] = state.token // Set header Authorization
                try {
                    const result = await axios.get('auth/currentuser') // axios get auth data from backend
                    const auth = result.data.auth // const a result auth data from backend
                    localStorage.setItem('auth_item', qs.stringify(auth)) // Set a auth_item in localStorage ( use qs.stringify )
                    context.commit('SetAUTH', qs.stringify(auth)) // Set Mutations auth data ( use qs.stringify )
                    return Promise.resolve(result) // return success result to a front
                } catch (err) {
                    return Promise.reject(err) // return error result to a front
                }
             }
            },
        async signIn (context, credentials) { // SignIn
            if(!context.getters.signInned) { // Only Visitor
                 try {
                     const result = await axios.post('auth/signin',{ // axios post data to backend ( use qs.stringify)
                         email: credentials.email, // email or username u want send to backend
                         password: credentials.password
                     })
                     const token = result.data.token // const a result data from backend
                     localStorage.setItem('access_token', token) // Set a token in localStorage
                     context.commit('SetTOKEN', token) // Set Mutations a token
                     context.dispatch('fetchAuth') // use Actions fetchAuth
                     return Promise.resolve(result) // return success result to a front
                 } catch (err) {
                     return Promise.reject(err) // return error result to a front
                 }
             }
            },
    async signUp (context, userdata) { // SignUp
        if(!context.getters.signInned) { // Only Visitor
            try {
                const result = await axios.post('auth/signup', { // axios post data to backend ( use qs.stringify )
                    username: userdata.username,
                    email: userdata.email,
                    password: userdata.password
                })
                const token = result.data.token // const a result data from backend
                localStorage.setItem('access_token', token) // Set a token in localStorage
                context.commit('SetTOKEN', token) // Set Mutations a token
                context.dispatch('fetchAuth') // use Actions fetchAuth
                return Promise.resolve(result) // return success result to a front
            } catch (err) {
                return Promise.reject(err) // return error result to a front
            }
        }
    },
     async signOut (context) { // SignOut ( with No axios )
        if(context.getters.signInned) { // Only Auth
            try {
                localStorage.removeItem('access_token') // remove access_token in localStorage
                localStorage.removeItem('auth_item') // remove auth_item in localStorage
                context.commit('DestroyTOKEN') // Set Mutations a token ( null )
                context.commit('DestroyAUTH') // Set Mutations a auth ( {} )
                return Promise.resolve('SignOut successfully') // return success result to a front
            } catch (err) {
                return Promise.reject('SignOut has error') // return error result to a front
            }
        }
    },
    // Google SignIn & SignUp
    async googleSignIn (context) {
     if(!context.getters.signInned) {
         try {
             const googleData = await Vue.prototype.$gAuth.signIn() // Popup google signin
             const access_token = googleData.Zi.access_token
             const results = await axios.post('auth/oauth/google', // Post access token from google to JWT token
                 null,  // data will be null to pass params
                 { params: { access_token }})
             const token = results.data.token // Generate JWT token
             localStorage.setItem('access_token', token) // Set a token to localStorage
             context.commit('SetTOKEN', token) // Mutations a token
             context.dispatch('fetchAuth') // Actions a current user
             return Promise.resolve(token)
         } catch (err) {
             return Promise.reject(err) // return error results
         }
     }
    },
    async facebookSignIn (context, tokendata) {
        if(!context.getters.signInned) {
            try {
                const access_token = await tokendata
                const results = await axios.post('auth/oauth/facebook',
                    null,
                    { params: { access_token }})
                const token = results.data.token
                localStorage.setItem('access_token', token)
                context.commit('SetTOKEN', token)
                context.dispatch('fetchAuth')
                return Promise.resolve(token)
            }catch (err) {
                return Promise.reject(err)
            }
        }
    }
}

// Export auth
export default {
    state,
    getters,
    mutations,
    actions
}
