import axios from 'axios'

const state = {
    currentOrder: {},
    loading: false
}

const getters = {
    currentOrder: (state) => state.currentOrder,
    currentOrderLoading: (state) => state.loading
}

const mutations = {
    SetCurrentOrder (state, payload) {
        state.currentOrder = payload
    },
    SetCurrentOrderLoading (state, payload) {
        state.loading = payload
    }
}

const actions = {
    async fetchCurrentOrder (context, id) {
        context.commit('SetCurrentOrderLoading', true)
        const results = await axios.get('order/current-order', { params: { _id: id } })
        const order = results.data.order
        context.commit('SetCurrentOrder', order)
        setTimeout(() => {
            context.commit('SetCurrentOrderLoading', false)
        }, 200)
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}
