import Vue from 'vue'
import Router from 'vue-router'
import store from './store/store'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
    // Reset scroll to top
    scrollBehavior: (to, from, savedPosition) => {
        if (savedPosition) {
            return savedPosition;
        } else if (to.hash) {
            return {
                selector: to.hash
            };
        } else {
            return { x: 0, y: 0 }
        }
    },
  // Router
  routes: [
      {
        path: '/test',
        name: 'Test',
        component: () => import('./views/Test')
      },
      {
          path: '/may-nelamith',
          name: 'MayNelamith',
          component: () => import('./views/May')
      },
      {
        path: '/message',
        component: () => import('./views/message/Message'),
          meta: {
            requiresAuth: true
          },
        children: [
            {
              path: '/',
                name: 'Message',
                component: () => import('./views/message/MessageIndex')
            },
            {
                path: ':id',
                name: 'MessageFocus',
                component: () => import('./views/message/MessageFocus')
            }
        ]
      },
      {
          path: '/',
          name: 'Home',
          component: () => import('./views/home/Home'),
      },
      {
        path: '/detail/:id',
        name: 'Details',
        component: () => import('./views/detail/Details')
      },
      {
        path: '/search',
        name: 'Search',
        component: () => import('./views/search/Search')
      },
      {
          path: '/signin',
          name: 'SignIn',
          component: () => import('./views/auth/SignIn'),
          meta: {
              requiresVisitor: true
          }
      },
      {
          path: '/signup',
          name: 'SignUp',
          component: () => import('./views/auth/SignUp'),
          meta: {
              requiresVisitor: true
          }
      },
      {
          path: '/signout',
          name: 'SignOut',
          component: () => import('./views/auth/SignOut'),
          meta: {
              requiresAuth: true
          }
      },
      {
        path: '/payment/:id',
        name: 'Payment',
        component: () => import('./views/payment/Payment')
      },
      {
          path: '/dashboard',
          component: () => import('./views/dashboard/Dashboard'),
          meta: {
            requiresAuth: true
          },
          children: [
              {
                  path: '',
                  name: 'Dashboard',
                  component: () => import('./views/dashboard/Index'),
              },
              {
                  path: 'profile/edit',
                  name: 'DashboardProfileEdit',
                  component: () => import('./views/dashboard/ProfileEdit'),
              },
              {
                path: 'task',
                name: 'DashboardTask',
                component: () => import('./views/dashboard/Task'),
                  meta: {
                      requiresFreelancer: true
                  }
              },
              {
                  path: 'feedback',
                  name: 'DashboardFeedback',
                  component: () => import('./views/dashboard/Feedback'),
                  meta: {
                      requiresFreelancer: true
                  }
              },
              {
                  path: 'portfolio/post',
                  name: 'DashboardPortfolioPost',
                  component: () => import('./views/dashboard/portfolio/Post'),
                  meta: {
                      requiresFreelancer: true
                  }
              },
              {
                  path: 'portfolio/update/:id',
                  name: 'DashboardPortfolioUpdate',
                  component: () => import('./views/dashboard/portfolio/Update'),
                  meta: {
                      requiresFreelancer: true
                  }
              }
          ]
      }
  ]
})

// requiresAuth || only auth access
router.beforeEach((to, from, next) => {
    if(to.matched.some(record => record.meta.requiresAuth)) {
        if(!store.getters.signInned) {
            next({ name: 'SignIn' })
        } else {
            next()
        }
    } else {
        next()
    }
})
// requiresVisitor || auth can't access
router.beforeEach((to, from, next) => {
    if(to.matched.some(record => record.meta.requiresVisitor)) {
        if(store.getters.signInned) {
            next({ name: 'Home' })
        } else {
            next()
        }
    } else {
        next()
    }
})
//
// requiresFreelancer
router.beforeEach((to, from, next) => {
    if(to.matched.some(record => record.meta.requiresFreelancer)) {
        if(store.getters.auth.type !== 'freelancer') {
            next({ name: 'Home' })
        } else {
            next()
        }
    } else {
        next()
    }
})

// Export router
export default router
