<template>
    <v-form @submit.prevent="search" style="min-width: 380px">
        <v-card-actions>
            <v-text-field
                    v-model="text"
                    solo
                    color="blue darken-1"
                    clearable
                    dense
                    hide-details
                    single-line
                    rounded
                    prepend-inner-icon="search"
                    placeholder="ຄົ້ນຫາ..."
            ></v-text-field>
            <!--                   <v-btn type="submit" icon>-->
            <!--                       <i-->
            <!--                               class="fas fa-search fa-2x search-icon"-->
            <!--                       ></i>-->
            <!--                   </v-btn>-->
        </v-card-actions>
    </v-form>
</template>

<script>
    export default {
        data: () => ({
           text: ''
        }),
        methods: {
            search () {
               if(this.$route.query.q !== this.text && this.text !== '') {
                   this.$router.push({ name: 'Search', query: { q: this.text} })
                   this.text = ''
               }
               this.text = ''
            }
        }
    }
</script>
